:root {
  --text: #ffe099;
  --bg: #000000e5;
  --btn-bg: #ffe099;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 100%;
}

body {
  color: var(--text);
  background: var(--bg);
  font-family: Raleway, sans-serif;
}

#heading {
  text-align: center;
  margin-bottom: 10px;
  font-size: 2.5rem;
}

#strategy {
  text-align: center;
}

.container {
  height: 100vh;
  width: 100vw;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.board {
  width: 60vw;
  flex-wrap: wrap;
  margin-top: 1rem;
  font-size: 6.25rem;
  display: flex;
}

.box {
  width: 20vw;
  height: 20vh;
  color: var(--text);
  text-align: center;
  border: 2px solid var(--text);
  cursor: pointer;
  justify-content: center;
  align-items: center;
}

#restart {
  background: var(--btn-bg);
  color: var(--bg);
  cursor: pointer;
  border: none;
  border-radius: 10px;
  margin-top: 2rem;
  padding: 15px 20px;
  font-size: 20px;
  font-weight: bold;
}

#restart:hover {
  transform: translateY(-3px);
  box-shadow: 1px 8px 13px #292929a8;
}

.zero, .one, .two {
  border-top: none;
}

.six, .seven, .eight {
  border-bottom: none;
}

.two, .five, .eight {
  border-right: none;
}

.zero, .three, .six {
  border-left: none;
}

@media only screen and (max-width: 35.5rem) {
  html {
    font-size: 80%;
  }

  .board {
    width: 75vw;
    justify-content: center;
    align-items: center;
    font-size: 9rem;
  }

  .box {
    width: 25vw;
    height: 25vh;
  }
}

@media only screen and (max-width: 26.5625rem) {
  .board {
    font-size: 8.5rem;
  }
}

@media only screen and (max-width: 20rem) {
  html {
    font-size: 62.5%;
  }

  .board {
    width: 80vw;
    font-size: 10rem;
  }
}

